.dataHolder {
  position: fixed;
  border-radius: 5px;
  box-shadow: 2px 2px 2px grey;
  top: 20px;
  /* bottom: 400px; */
  height: 650px;
  left: 20px;
  display: inline-block;
  width: 320px;
  background: linear-gradient(180deg, #ffffff 10%, #2a3c47 10%);
  z-index: 5 !important;
  padding: 6px;
}

.nav {
  position: absolute;
  color: #ffffff;
  top: 0;
  z-index: 4 !important;
}

.title {
  position: fixed;
  color: #2a3c47;
  text-shadow: 2px 2px 4px #ffffff;
  left: 30px;
  top: 10px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: lighter;
  font-size: 48px;
}

.listStyle {
  width: 800px;
}

.logo_home {
  width: 250px;
  top: 4px;
}

.incidentWidth {
  position: fixed;
  top: 60px;
  left: 20px;
  height: 1200px;
}

.clientWidth {
  position: fixed;
  top: 60px;
  left: 940px;
  height: 1200px;
}

.loginExplainer {
  position: fixed;
  top: 200px;
  border-radius: 5px;
  box-shadow: 2px 2px 2px grey;
  left: 300px;
  display: inline-block;
  width: 550px;
  height: 600px;
  background: linear-gradient(180deg, #ffffff 10%, #2a3c47 10%);
  z-index: 5 !important;
  padding: 6px;
}
.loginHolder {
  position: fixed;
